
.main {
  width: 1160px;
  margin: 0 auto;
  text-align: left;
  margin-top: 30px;
  font-weight: 600;
  font-family: PingFangSC-Semibold, PingFang SC;
}
*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
blockquote,
body,
button,
dd,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
input,
legend,
li,
ol,
p,
pre,
td,
textarea,
th,
ul {
  margin: 0;
  padding: 0;
}
body {
  font-size: 14px;
  background-color: #FAFAFA;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}
ol,
ul {
  list-style: none;
}
textarea {
  font-family: inherit;
}
address,
cite,
dfn,
em,
var {
  font-style: normal;
}
a {
  color: #404040;
  text-decoration: none;
}
a:not(.disabled):hover {
  color: #197AFF;
}
.text-right {
  text-align: right;
}
.text-center {
  text-align: center;
}
.cl,
.clearfix {
  *zoom: 1;
}
.cl:after,
.clearfix:after {
  content: ' ';
  display: block;
  height: 0;
  clear: both;
}
input:focus,
select:focus {
  outline: none;
  border-color: #9ecaed;
}
button {
  cursor: pointer;
  font-size: 16px;
  outline: none;
}
.content .blog:nth-child(3n) {
  margin-right: 0px;
}
.content .blog:nth-child(8) {
  margin-right: 0px;
}

.theme {
  height: 450px;
  width: 100%;
  background: #fff;
  display: inline-block;
  margin-bottom: 40px;
  font-size: 0;
  vertical-align: top;
  font-family: PingFangSC-Regular, PingFang SC;
}
.theme-text {
  padding: 50px 0 0 50px;
  display: inline-block;
  vertical-align: top;
  position: relative;
  height: 450px;
  width: 460px;
}
.theme-text img {
  width: 54px;
  height: 54px;
}
.theme-text .title {
  margin-top: 50px;
  margin-bottom: 20px;
  font-size: 24px;
  line-height: 33px;
  font-weight: 500;
  color: #404040;
}
.theme-text .description {
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: #807c7c;
}
.theme-text .label {
  position: absolute;
  bottom: 30px;
  font-size: 12px;
  line-height: 17px;
  font-weight: 400;
  color: #807c7c;
}
.theme-image {
  display: inline-block;
  vertical-align: top;
}
.theme-image .image-wrapper {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  height: 450px;
  width: 700px;
}
.theme-image .image-wrapper img {
  max-height: 450px;
  max-width: 700px;
}

.feature {
  height: 450px;
  width: 760px;
  background: #fff;
  display: inline-block;
  margin-bottom: 40px;
  margin-right: 35px;
  font-size: 0;
  vertical-align: top;
  font-family: PingFangSC-Regular, PingFang SC;
}
.feature-text {
  padding: 50px 0 0 50px;
  display: inline-block;
  vertical-align: top;
  position: relative;
  height: 450px;
  width: 390px;
}
.feature-text img {
  width: 54px;
  height: 54px;
}
.feature-text .title {
  margin-top: 50px;
  margin-bottom: 20px;
  font-size: 24px;
  line-height: 33px;
  font-weight: 500;
  color: #404040;
}
.feature-text .description {
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: #807c7c;
}
.feature-text .label {
  position: absolute;
  bottom: 30px;
  font-size: 12px;
  line-height: 17px;
  font-weight: 400;
  color: #807c7c;
}
.feature-image {
  display: inline-block;
  vertical-align: top;
}
.feature-image .image-wrapper {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  height: 450px;
  width: 370px;
}
.feature-image .image-wrapper img {
  max-height: 450px;
  max-width: 370px;
}

.blog {
  height: 450px;
  width: 360px;
  background: #fff;
  display: inline-block;
  margin-bottom: 40px;
  margin-right: 40px;
  font-size: 0;
  vertical-align: top;
  position: relative;
  font-family: PingFangSC-Regular, PingFang SC;
}
.blog-image {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  height: 240px;
  width: 360px;
}
.blog-image img {
  max-height: 240px;
  max-width: 360px;
}
.blog-content {
  padding: 32px 0 0 30px;
}
.blog-content .title {
  margin-top: 0;
  margin-bottom: 13px;
  font-size: 24px;
  line-height: 33px;
  font-weight: 500;
  color: #404040;
}
.blog-content .description {
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: #807c7c;
}
.blog-content .label {
  position: absolute;
  bottom: 30px;
  font-size: 12px;
  line-height: 17px;
  font-weight: 400;
  color: #807c7c;
}

nav {
  height: 42px;
  line-height: 42px;
}
nav img {
  width: 42px;
  height: 42px;
}
nav ul {
  float: right;
}
nav ul li {
  display: inline-block;
  margin-right: 40px;
}
.title {
  line-height: 56px;
  font-size: 36px;
  margin: 112px 0 104px 0;
  font-family: PingFangSC-Semibold, PingFang SC;
}
.title p {
  color: #404040;
}
.title p:last-child {
  color: #807c7c;
}

footer {
  height: 96px;
  line-height: 96px;
  color: #9e9e9e;
  border-top: 1px solid #e8e8e8;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
}
footer .share {
  float: right;
}

